<template>
    <div :class="['DecorBody']">
      <div class="btn" @click="detalized=!detalized">{{btnName}}</div>
      <div v-for="card in cardArr" :key="card.id" :class="[(!detalized && card.type=='unit') ? 'inline' : '']">
        <div v-if="card.type=='header1'" class="group">{{card.text}}</div>
        <div v-if="card.type=='header2'" class="subgroup">{{card.text}}</div>
        <decor-card  v-if="card.type=='unit'" :params="card" :class="[detalized ? '' : 'min']"></decor-card>
      </div>

    </div>
</template>

<script>
    import DecorCard from "./DecorCard";
    import axios from 'axios'
    import {host, b_ax} from './main'

    export default {
        name: "DecorBody",
        components: {DecorCard},
        data: function () {
              return {
                cardArr:[],
                detalized: 1,
                host:host,
            }
        },
        props:[],
        computed:{
          btnName: function () {
            if (this.detalized)return'Свернуть';
            else return'Развернуть';
          },
        },
        created: function () {
          //console.log('Arrr')
          let vm = this;
          //var fd = {'offset': 0,'size':3,'filter':''}
          b_ax(axios).get(host+'/units/get-my-units')
          .then(function(data){
             vm.cardArr = data.data;
              vm.$forceUpdate();
            }
          );
        }
    }
</script>

<style lang="sass" scoped>
    $lgray: #f6f6f6
    $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
    .DecorBody
      //background-color: lightseagreen
      //padding-left: 20px
      .btn
        display: inline-block
        float: right
        background-color: white
        //border: solid black 1px
        padding: 10px 0 10px 0
        height: 17px
        width: 143px
        font-size: 15.5px
        text-align: center
        margin-top: -20px
        margin-right: 48px
        box-shadow: $shadow
      .group
        font-size: 32px
        margin: 15px 0 10px 0
      .subgroup
        font-size: 24px
        margin: 10px 0 10px 0
      .inline
        display: inline-block
</style>



