<template>
  <div class="SearchBar">
    <input v-bind:placeholder="text"/>
    <img :src="host+'/static/img/shared/loupe-24.png'" />
  </div>
</template>

<script>
  import {host} from "./main";

  export default {
      name: "SearchBar",
      props: ['text'],
      data: function () {
        return{
          host:host,
        }
      }
    }
</script>

<style lang="sass" scoped>
  .SearchBar
    display: inline-block
    width: 380px
    input
      border: solid lightgray 1px
      width: 300px
      height: 37px
      vertical-align: middle
      font-size: 18px
      font-family: Philosopher
      padding-left: 30px
      padding-right: 40px
    img
      height: 24px
      vertical-align: top
      margin-top: 6px
      margin-left: -37px
</style>



