<template>
  <div v-if="shown">
    <div class="bg"></div>
    <div class="DecorAdd" >
      <color-picker :shown="showColorPicker" :colorData="colorData" @hideColorPicker="onHideColorPicker"></color-picker>
      <pick-bar :shown="showMaterialPicker" :arr="matArr" :len="matLen" :showLim="matShowLim" :pickLim="matPickLim" @done="updateMaterials"></pick-bar>
      <decor-add-fill :shown="showAddFill" @hideAddFill="showAddFill=false"></decor-add-fill>
      <div class="PicPanel">
        <div class="Pic" v-for="(pic, index) in pics">
          <img class="picImg" :src="pic ? pic : host+'/static/img/grouppreview/noimage.png'" @click="$refs.input[index].click()"/>
          <input class='hidden' type="file" :ref="'input'" :file="pic" @change="updatePics($event,index)">
        </div>
      </div>
      <div class="RightPanel">
        <div class="TopPanel">
          <div class="GenParams">
            <div class="GenParam" v-for="param in genParams">
              <div class="ParamName">{{param.name}}</div>
              <div v-if="param.name=='Цвет'" @click="showColorPicker=true" class="colorBar">
                <div class="color" v-for="c in colorArr" :style="'background-image: url('+c.texture+'); background-color: #'+c.rgb_hex+'; width: ' + 154/colorArr.length+'px;'"></div>
                <!--<img v-if="colorArr.length==0" :src="host+'/static/img/shared/color-field.png'" class="colorImg" @click="showColorPicker=true"/>-->
                <span v-if="colorArr.length==0">Не более 4 цветов</span>
              </div>
              <div class="materialBar" v-if="param.name=='Материал'" @click="showMaterialPicker=true"><span>{{addDict[param['alias']]}}</span></div>
              <input :class="[['Цвет','Материал'].includes(param.name) ? 'hidden' : '']" v-model="addDict[param['alias']]"/>
            </div>
          </div>
          <span></span>
          <div class="GroupParams" v-if="groups">
            <div class="Label">Категория</div>
            <div class="Groups">
              <div class="Group" v-for="(group,i) in groups" v-if="group&&groupOptions[groups[i]]">
                <!-- <label>{{group}}</label> -->
                <select  v-if="groupOptions[groups[i]].length" v-model="groups[i+1]" @change="pickGroup(i, groups[i+1])">
                  <option class="hidden" disabled></option>
                  <option v-for="g in groupOptions[groups[i]]" :value="g.id">
                    {{g.name}}
                  </option>
                </select>
              </div>
            </div>
            <div v-if="groupParams[groups[groups.length-1]]" class="Params">
                <div class="Param" v-for="param in groupParams[groups[groups.length-1]]['parameters']">
                  <div class="ParamName">{{param.name}}<span v-if="param.dimension">, {{param.dimension}}</span></div>
                  <input v-model="params[param.id]" @change="$forceUpdate()"/>
                </div>
            </div>
          </div>
          <img class="GroupImg" v-if="groupParams[groups[groups.length-1]]" :src="groupParams[groups[groups.length-1]]['picture']" :title="groupParams[groups[groups.length-1]]['picture']"/>
        </div>
        <div class="BotPanel">
          <div class="btn" @click="showAddFill=true">Заполнить за меня</div>
          <div class="BotParam">
            <div class="ParamName" >{{botParams[0].name}}</div>
            <input v-model="addDict[botParams[0]['alias']]"/>
          </div>
          <div class="BotParam">
            <div class="ParamName" >{{botParams[1].name}}</div>
            <textarea v-model="addDict[botParams[1]['alias']]"></textarea>
          </div>
          <div class="BtnBar">
            <div class="btn" @click="addDecorUnit()">Опубликовать</div>
            <div class="btn">Добавить на склад</div>
            <div class="btn" @click="hideAddDecor()">Отменить</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    import axios from 'axios'
    import DecorAddFill from "./DecorAddFill";
    import ColorPicker from "./ColorPicker";
    import {host, b_ax} from "./main";
    import PickBar from "./PickBar";

    export default {
        name: "DecorAdd",
      components: {PickBar, ColorPicker, DecorAddFill},
      props: ["shown"],
        data: function () {
            return {
              addDict:{'photo1':'','photo2':'','photo3':'','photo4':'','photo5':'', 'unit-materials':'','weight':'','bail':'','count':'',
                'title':'', 'sets':'[]','first-day-cost':'','rent-min-days':'','day-cost':'','unit-group':'','unit-colors':'[]','parameters':{}, 'keywords':'', 'description':''},
              pics:['','','','',''],
              groupOptions:{},
              genParams:[
                {'name':'Материал', 'alias':'unit-materials'}, {'name':'Цвет', 'alias':'unit-colors'},
                {'name':'Вес, кг', 'alias':'weight'}, {'name':'Минимальный срок аренды, сут', 'alias':'rent-min-days'},
                {'name':'Стоимость за первый день, р', 'alias':'first-day-cost'}, {'name':'Стоимость, р/сут', 'alias':'day-cost'},
                {'name':'Залог, р', 'alias':'bail'}, {'name':'Количество, шт', 'alias':'count'}, {'name':'Название', 'alias':'title'},
                {'name':'Коллекции', 'alias':'sets'},
              ],
              groups:[],
              params:{},
              groupParams:{},
              botParams:[{'name':'Ключевые слова', 'alias':'keywords'}, {'name':'Комментарий', 'alias':'description'}],
              showAddFill: false,
              showColorPicker: 0,
              showMaterialPicker:0,
              host:host,
              colorData:[],
              colorArr:[],
              matArr:[],
              matLen:1,
              matShowLim:6,
              matPickLim:4,
              buf:'',
            }
        },
        methods:{
          addGroupOptions: function (gid) {
            let v = this;
            if (v.groupOptions[gid]) return;


            let h = '/units/get-groups?parentid='+gid.toString();
            if (gid ==='root') {h = '/units/get-groups';}
            //console.log(h);

            b_ax(axios).get(h)
              .then(function(data){
              //if (gid ==='') gid='root';
              //console.log(data);
              v.groupOptions[gid] = data.data;
              //console.log(data.data);
              if (data.data.length===0) v.addGroupParam(gid);
              v.$forceUpdate();
              }
            );
            //console.log(this.groupOptions);
          },
          addGroupParam: function(gid){
            let v = this;
            if (v.groupParams[gid]) {
              this.updateParams();
              return;
            }


            let h = '/units/get-group-parameters?groupid='+gid.toString();
            //console.log(h);

            b_ax(axios).get(h)
              .then(function(data){
                  //if (gid ==='') gid='root';
                  //console.log(data);
                  let pd = JSON.parse(data.data.parameters);
                  /*for (let p in pd){
                    pd[p]['value']='';
                  }*/
                  v.groupParams[gid] = {picture:data.data.picture, parameters:pd};
                  v.updateParams();
                  v.$forceUpdate();
                }
              );
            //console.log(this.groupParams);
          },
          pickGroup: function (n, gid) {
            //console.log(gid);
            //console.log(n);
            this.groups = this.groups.slice(0, n+1);
            //console.log(this.groups);
            this.groups[n+1] = gid;
            this.addGroupOptions(gid);
            //console.log(this.groups);

          },
          updateParams: function(){
            this.params = {};
            var ps = this.groupParams[this.groups[this.groups.length-1]]['parameters'];
            for (let p in ps){
              //console.log(ps[p].name);
              this.params[ps[p].id] = '';
            }
          },
          hideAddDecor:function(){
            //console.log('hide ti');
            this.$emit('hide-add-decor');
            this.groups=['root'];
            this.colorArr=[];
            this.matArr=[];
          },
          updatePics :function(event,n){
            //console.log(event);
            var input = event.target;
            var vm = this;
            /*vm.pics[n] = '';
            vm.addDict['photo'+(n+1)] = '';*/
            if (input.files && input.files[0]){
              //console.log(input.files);
              var reader = new FileReader();
              reader.onload = function (e) {
                vm.pics[n] = e.target.result;
                vm.addDict['photo'+(n+1)] = e.target.result;
                //console.log(n);
                vm.$forceUpdate();
              }
              reader.readAsDataURL(input.files[0]);

            }
          },
          addDecorUnit:function(){
            var vm = this;
            var fd = new FormData;
            vm.addDict['unit-group'] = vm.groups[vm.groups.length-1];
            //vm.addDict['photo1'] = this.$refs.input.files[0];
            vm.addDict['parameters'] = JSON.stringify(vm.params);
            //console.log(vm.addDict);

            for ( var key in vm.addDict ) {
                fd.append(key, vm.addDict[key]);
            }

            fd.set('photo1', vm.$refs.input[0].files[0]);
            fd.set('photo2', vm.$refs.input[1].files[0]);
            fd.set('photo3', vm.$refs.input[2].files[0]);
            fd.set('photo4', vm.$refs.input[3].files[0]);
            fd.set('photo5', vm.$refs.input[4].files[0]);


            console.log(fd);
            //console.log(vm.$refs.input)
            b_ax(axios).get("/shared/get-csrf-token")
              .then(function(data1){
                  //console.log(data1.data);
                  //vm.addDict['csrfmiddlewaretoken'] = data1.data;
                  //axios.defaults.headers.post['X-CSRFToken'] = data1.data;
                  //axios.defaults.headers.post['Content-Type'] = 'application/json';
                  b_ax(axios).post("/units/add-new-unit", fd,{headers:{'X-CSRFToken':data1.data,'Content-Type': 'multipart/form-data'}})
                  .then(function(data){
                      console.log(data.data);
                    }
                  )
                  .catch(function(data){
                      console.warn(data.response.data);
                    }
                  )
              }
              )
              .catch(function(data){
                  console.warn(data.response.data);
                }
              )

          },
          onHideColorPicker:function(colorArr){
            this.colorArr=colorArr;
            let arr =[];
            for (let c in colorArr){
              arr.push(colorArr[c].id);
            }

            this.addDict['unit-colors']=JSON.stringify(arr);
            //this.buf = colorArr;
            this.showColorPicker=false;
            //this.$forceUpdate();
          },
          updateMaterials:function(idArr){
            this.addDict['unit-materials'] = JSON.stringify(idArr);
            this.showMaterialPicker = false;
          },
          info:function () {
            console.log(this.groups);
            console.log(this.groupOptions);
            console.log(this.groupParams);
          },
          test1:function (i) {
            this.$forceUpdate();
          },
          test2:function (i) {
            console.log(this.groupParams[56].toString());
          }

        },
        created: function () {
          let v = this;
          //this.pickGroup(-1,'root');
          //this.groups.pop();
          this.addGroupOptions('root');
          this.groups.push('root');
          //this.groupOptions.clear;
          b_ax(axios).get(host+'/units/color-picker-source')
            .then(function(data){
                //console.log(data.data);
                v.colorData = data.data;
                v.$forceUpdate();
              }
            );
          b_ax(axios).get(host+'/units/materials-source')
            .then(function(data){
                //console.log(data.data);
                v.matArr = data.data;
                v.$forceUpdate();
              }
            );

        }



    }
</script>

<style lang="sass" scoped>
    $lgray: #f6f6f6
    $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
    .bg
      background-color: darkgray
      opacity: 0.3
      position: fixed
      left: 0
      top: 0
      height: 100vh
      width: 100vw
      z-index: 0
    .DecorAdd
      position: fixed
      opacity: 1
      left: 145px
      top: 120px
      background-color: white
      border: solid darkgray 1px
      width: 1085px
      text-align: left
      //margin-top: -42px
      //margin-left: -243px
      box-shadow: $shadow
      font-family: Tahoma
      z-index: 1
      .DecorAddFill
        z-index: 2
      .ColorPicker
        z-index: 2
      .PickBar
        position: fixed
        z-index: 2
        left: 523px
        top: 149px
        width: 460px
      .PicPanel
        display: inline-block
        margin: 28px 10px 0 15px
        .Pic
          display: inline-block
          .picImg
            cursor: pointer
            height: 40px
            width: 40px
            margin: 8px 8px 0 0
            background-color: lightgrey
            vertical-align: top
          &:first-child
            display: block
            //margin: 0
            .picImg
              width: 186px
              height: 186px
              margin: 0
        button
          display: block
      .RightPanel
        display: inline-block
        vertical-align: top
        font-size: 18px
        margin-top: 28px
        //margin-left:
        span
          vertical-align: top
        .TopPanel
          //background-color: aliceblue
          display: inline-block
          min-height: 200px
          //width: 900px
          .GenParams
            //text-align: right
            display: inline-block
            .materialBar
              vertical-align: middle
              width: 154px
              height: 30px
              display: inline-block
              cursor: pointer
              text-align: center
              //background-color: $lgray
              border: darkgray solid 0.1px
              font-size: 16px
              span
                //border: darkgray solid 0.1px
                padding: 3px 9px 7px 8px
                font-size: 16px
                vertical-align: middle
            .colorBar
              vertical-align: middle
              width: 154px
              height: 30px
              display: inline-block
              cursor: pointer
              text-align: center
              //background-color: $lgray
              .color
                height: 30px
                display: inline-block
                background-size: cover
              span
                border: darkgray solid 0.1px
                padding: 3px 9px 7px 8px
                font-size: 16px
                vertical-align: middle
              img
                height: 30px
                width: 154px
            .GenParam
              margin-bottom: 10px
              .ParamName
                width: 150px
                display: inline-block
              input
                width: 150px
                height: 22px
          .GroupParams
            display: inline-block
            vertical-align: top
            margin-left: 25px
            margin-right: 23px
            .Label
              //margin: 0px 0px 0px 0px
              width: 150px
              display: inline-block

            .Groups
              //width: 230px
              display: inline-block
              vertical-align: top
              //margin-top: 10px
              .Group
                margin: 0px 0px 10px 0px
                select
                  height: 27px
                  width: 153px
            .Params
              margin-top: 30px
              .Param
                margin: 0 0 10px 0
                .ParamName
                  width: 150px
                  display: inline-block
                input
                  width: 150px
                  height: 22px
          .GroupImg
            width: 170px
            height: 170px
            vertical-align: top
            display: inline-block
            border: solid gray 1px
        .BotPanel
          .btn
            display: inline-block
            background-color: white
            padding: 23px 0 23px 0
            font-size: 21px
            box-shadow: $shadow
            text-align: center
            position: absolute
            margin: 105px 0 0 -200px
            width: 327px
            font-family: Philosopher
            z-index: 1
          .BotParam
            margin-bottom: 10px
            .ParamName
              vertical-align: top
              width: 150px
              display: inline-block
            input
              width: 690px
              height: 22px
            textarea
              width: 690px
              height: 134px
              resize: none
              padding: 1px
          .BtnBar
            text-align: left
            margin: 30px 0px 30px 113px
            .btn
              width: 200px
              //height: 37px
              padding: 10px 0 10px 0
              margin: 0 0px 0 42px
              //margin: 15px 20px 20px 0
              font-size: 18px
              position: inherit


</style>



