<template>
    <div class="DecorHeader">
      <div class="btn" @click="showAddDecor = true">+ Добавить предмет</div>
      <span> <search-bar text="Поиск в моем декоре"></search-bar></span>
      <span class="counter">Загружено товаров 63/100</span>
      <decor-add :shown="showAddDecor" @hide-add-decor="showAddDecor = false"></decor-add>
    </div>
</template>

<script>
    import DecorAdd from "./DecorAdd";
    export default {
      name: "DecorHeader",
      components: {DecorAdd},
      data: function () {
            return {
              showAddDecor: 0
            }
        },
    }
</script>

<style lang="sass" scoped>
    $lgray: #f6f6f6
    $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
    .DecorHeader
      //background-color: lightpink
      //text-align: right
      .btn
        display: inline-block
        background-color: white
        //border: solid $lgray 1px
        padding: 8px 0 8px 20px
        //margin: 5px
        box-shadow: $shadow
        font-size: 18px
        width: 188px
      .counter
        //margin: 5px
        //width: 200px
        font-size: 14px
        float: right
        margin: 10px 47px 0 0
        font-family: Tahoma
      .SearchBar
        //margin-right: 25px
        margin-left: 47px

</style>



