<template>
    <div class="DecorCard">
      <div class="PicPanel">
        <img class="BigImg" :src="params.photo1 ? params.photo1 : host+'/static/img//grouppreview/noimage.png'"/>
        <div class="smallPanel">
          <img class="SmallImg" :src="params.photo2 ? params.photo2 : host+'/static/img//grouppreview/noimage.png'"/>
          <img class="SmallImg" :src="params.photo3 ? params.photo3 : host+'/static/img//grouppreview/noimage.png'"/>
          <img class="SmallImg" :src="params.photo4 ? params.photo4 : host+'/static/img//grouppreview/noimage.png'"/>
          <img class="SmallImg" :src="params.photo5 ? params.photo5 : host+'/static/img//grouppreview/noimage.png'"/>
        </div>

      </div>
      <div class="RightPanel">
        <div class="TopPanel">
          <div class="DecorName">{{params.title}}</div>
          <div class="col1">
            <div class="line" v-for="p in params.parameters">
              <div class="name">{{p.name}} = </div> <div class="val">{{p.value}} {{p.dimension}}</div>
            </div>
          </div>
          <div class="col">
            <div class="line">
              <div class="name">Материал: </div><div v-for="(m, i) in materialArr" class="val text">{{m+dot(i)}}</div>
            </div>
            <div class="line">
              <div class="name">Цвет: </div>
              <div class="val">
                <div class="colorBar">
                  <div class="color" v-for="c in params.colors" :style="'background-image: url('+host+'/static/img/units/texture/'+c.texture+'); background-color: #'+c.rgb_hex+'; width: ' + 100/params.colors.length+'px;'"></div>
                  <img v-if="params.colors.length==0" :src="host+'/static/img/shared/color-field.png'" class="colorImg"/>
                </div>
              </div>
            </div>
            <div class="line">
              <div class="name">Вес: </div> <div class="val">{{params.weight}} кг</div>
            </div>
            <div class="line count">
              <div class="name">Количество: </div> <div class="val">{{params.count}} шт.</div>
            </div>
          </div>
          <div class="col">
            <div class="line">
              <div class="name">Первый день: </div> <div class="val">{{params.first_day_cost}} р/сут</div>
            </div>
            <div class="line">
              <div class="name">Аренда от: </div> <div class="val">{{params.rent_min_days}} суток</div>
            </div>
            <div class="line">
              <div class="name">Аренда: </div> <div class="val">{{params.day_cost}} р/сут</div>
            </div>
            <div class="line">
              <div class="name">Залог: </div> <div class="val">{{params.bail}} р</div>
            </div>
          </div>
        </div>
        <div class="Btns">
          <div class="status">Заказов нет</div>
          <div class="btn">Редактировать</div>
          <div class="btn">{{postBtn}}</div>
          <div class="btn">Удалить</div>
        </div>
        <div class="BotPanel">
          <div class="line">
            <div class="name">Коллекция: </div> <div class="val">{{params.sets}}</div>
          </div>
          <div class="line">
            <div class="name">Ключевые слова: </div> <div class="val" ><span v-for="w in params.keywords">#{{w.word}}</span></div>
          </div>
          <div class="line" v-if="params.commentary">
            <div class="name">Комментарий: </div> <div class="val">{{params.commentary}}</div>
          </div>
        </div>
      </div>


    </div>
</template>

<script>
    import BtnBar from "./BtnBar";
    import {host} from "./main";

    export default {
        name: "DecorCard",
        components: {BtnBar},
        props: ["params"],
          data: function () {
              return {
                posted: true,
                host:host,
              }
          },
        computed:{
          postBtn: function () {
            if (this.posted) return 'Убрать с сайта'
            else return 'Добавить на сайт'
          },
          materialArr:function () {
            let arr = [];
            for (let m in this.params.materials){
              arr.push(this.params.materials[m].name);
            }
            return arr;
          }
        },
      methods:{
          dot:function (i) {
            if (i<this.materialArr.length-1) return ',';
            else return '';
          }
      }
    }
</script>

<style lang="sass" scoped>
  $lgray: #f6f6f6
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .DecorCard
    background-color: $lgray
    //border: solid  1px
    box-shadow: $shadow
    width: 847px
    margin-bottom: 20px
    .line
      .name
        display: inline-block
        vertical-align: top
        margin-bottom: 10px
        margin-right: 5px
      .val
        display: inline-block
        vertical-align: top
        margin-bottom: 10px

      .text
        display: inline-block
        margin-right: 5px
      .colorBar
        vertical-align: top
        display: inline-block
        height: 16px
        .color
          height: 18px
          display: inline-block
          background-size: cover
        img
          height: 18px
          width: 100px

    .PicPanel
      //background-color: lightgreen
      margin: 9px 0 0 11px
      display: inline-block
      img
        background-color: lightgrey
        //border: solid  1px
      .BigImg
        width: 186px
        height: 186px
        margin: 0
        padding: 0
      .smallPanel
        height: 40px
        margin: 0
        padding: 0
        .SmallImg
          height: 40px
          width:  40px
          display: inline
          margin-top: 4px
          margin-right: 4px
    .RightPanel
      display: inline-block
      vertical-align: top
      width: 630px
      .TopPanel
        //background-color: aliceblue
        display: inline-block
        min-height: 200px
        .DecorName
          font-size: 24px
          //font-weight: bold
          margin-top: 10px
          //margin: 10px
        .col1
          display: inline-block
          vertical-align: top
          margin: 30px -5px 0 0
          font-size: 18px
          max-width: 120px
          overflow-y: auto
        .col
          display: inline-block
          vertical-align: top
          margin: 30px 10px 0 20px
          font-size: 15.5px
          max-width: 150px
          overflow-y: auto
      .BotPanel
        margin-bottom: 20px
        .val span
          margin-right: 10px
      .Btns
        float: right
        margin-top: 10px
        .btn
          display: block
          background-color: white
          border: solid lightgrey 1px
          padding: 10px 0px 10px 0px
          margin-bottom: 10px
          text-align: center
          box-shadow: $shadow
          width: 171px
        .status
          text-align: right
          margin-bottom: 40px
          color: black
          box-shadow: none
  .min.DecorCard
    //background-color: lightskyblue
    //padding: 5px
    width: fit-content
    //text-align: center
    box-shadow: none
    display: inline-block
    margin-right: 10px
    .PicPanel
      display: block
      margin: 0
      .BigImg
        box-shadow: $shadow
      .smallPanel
        display: none
    .RightPanel
      display: block
      width: inherit
      .TopPanel
        //display: block
        min-height: fit-content
        text-align: left
        width: inherit
        .DecorName
          font-size: 18px
          font-weight: normal
          margin: 0px
          display: none
        .col
          margin: 0
        .col1
          margin: 0
        .line
          display: none
        .line.count
          display: inline-block
          .name
            display: none
          .val
            margin: 0
            padding: 5px
      .Btns
        display: none
      .BotPanel
        display: none

</style>



