<template>
  <div v-if="shown" >
    <div class="bg"></div>
    <div class="ColorPicker">
        <div class="content">
          <div class="line">
            <div class="bar1 bar" v-for="b in colorData['group1']" :style="'background-color: #'+b.rgb_hex" @click="pick(b)"><img :class="picked.includes(b) ? '' : 'hidden'" :src="host+'/static/img/units/border_38_112_2.png'"/></div>
          </div>
          <div class="line">
            <div class="bar1 bar" v-for="b in colorData['group2']" :style="'background-color: #'+b.rgb_hex" @click="pick(b)"><img :class="picked.includes(b) ? '' : 'hidden'" :src="host+'/static/img/units/border_38_112_2.png'"/></div>
          </div>
          <div class="line">
            <div class="bar2 bar" v-for="b in colorData['group3']" :style="'background-color: #'+b.rgb_hex" @click="pick(b)"><img :class="picked.includes(b) ? '' : 'hidden'" :src="host+'/static/img/units/border_182_38_2.png'"/></div>
          </div>
          <div class="line">
            <div class="bar3 bar" v-for="b in colorData['group4']" :style="'background-image: url('+b.texture+');'" @click="pick(b)"><img :class="picked.includes(b) ? '' : 'hidden'" :src="host+'/static/img/units/border_86_112_2.png'"/></div>
          </div>
          <div class="line">
            <div class="bar3 bar" v-for="b in colorData['group5']" :style="'background-image: url('+b.texture+');'" @click="pick(b)"><img :class="picked.includes(b) ? '' : 'hidden'" :src="host+'/static/img/units/border_86_112_2.png'"/></div>
          </div>
          <div class="btns">
            <div class="btn" @click="picked=applied.slice(); $emit('hideColorPicker',applied); ">Отмена</div>
            <div class="btn" @click="applied=picked.slice(); $emit('hideColorPicker',applied); ">Подтвердить</div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
  import axios from "axios"
  import {host, b_ax} from './main'
    export default {
      name: "ColorPicker",
      props:['shown','colorData'],
      data: function () {
          return {
            //colorData: {},
            picked:[],
            applied:[],
            host:host,
          }
      },
      methods:{
        pick:function (arg) {
          let max = 5;
          if (this.picked.includes(arg)) this.picked.splice(this.picked.indexOf(arg),1);
          else {
            if (this.picked.length>max-1) {
              alert('Допустимо не более '+max+' цветов!');
              return
            }
            this.picked.push(arg);
          }
          console.log(arg);
          console.log(this.picked);

        }
      },
      /*created: function () {
        console.log('Arrr')
        let v = this;
        b_ax(axios).get(host+'/units/color-picker-source')
          .then(function(data){
              //if (gid ==='') gid='root';
              //console.log(data);
              //v.groupOptions[gid] = data.data;
              console.log(data.data);
              v.colorData = data.data;
              v.$forceUpdate();
            }
          );
      }*/
    }
</script>


<style lang="sass" scoped>
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .bg
    background-color: darkgray
    opacity: 0.3
    position: fixed
    left: 0
    top: 0
    height: 100vh
    width: 100vw
    z-index: 2
  .ColorPicker
    background-color: white
    //border: solid lightgray 1px
    box-shadow: $shadow
    height: 710px
    width: 1010px
    position: absolute
    margin: -5px 0px 0px 40px
    z-index: 2
    .content
      margin-top: 23px
      margin-left: 35px

      .line
        margin-bottom: 18px
        .bar
          margin-right: 10px
          //background-color: black
          cursor: pointer
          display: inline-block
          box-shadow: $shadow
        .bar1
          height: 112px
          width: 38px
        .bar2
          height: 38px
          width: 182px
        .bar3
          height: 112px
          width: 86px
      .btns
        text-align: center
        margin-top: 40px
        .btn
          text-align: center
          display: inline-block
          margin-right: 45px
          padding-top: 8px
          font-family: Philosopher
          font-size: 18px
          box-shadow: $shadow
          height: 30px
          width: 200px



</style>



