<template>

    <div class="PickBar" v-if="shown" >
      <div class="bg" @click="$emit('done', idArr)"></div>
      <div class="body">
        <div class="topPanel">
          <div v-for="p in pickedArr" class="picked">
            <span class="text" @click="removeVariant(p)">{{p.name+','}}</span>
          </div>
          <input v-model="val" :class="[valid ? '' : 'invalid']" @keyup.enter="pickWord(val)" @keyup.esc="$emit('done', idArr)"/>
        </div>
        <div class="variants" v-if="showVariants" >
          <div v-for="item in displayed" class="variant" @click="pickWord(item.name)" >{{item.name}}</div>
        </div>
      </div>

    </div>

</template>

<script>
  import {host} from "./main";
    export default {
      name: "PickBar",
      props:['arr', 'len', 'showLim', 'pickLim', 'shown'],
      data: function () {
          return {
            host:host,
            val: '',
            showVariants:false,
            valid: true,
            idArr:[],
            pickedArr:[],
          }
      },
      computed:{
        words:function(){
          return this.val.split(', ');
        },
        lastWord: function(){
          return this.words[this.words.length-1];
        },
        displayed: function () {
          //this.showVariants=true;
          var vm = this;
          //this.updateWords();
          var re = new RegExp('(\\s+'+this.lastWord+')|(^'+this.lastWord+')','i');
          var ans = this.arr.filter(variant => (re.exec(variant.name) && !vm.pickedArr.includes(variant))).slice(0,this.showLim);

          //console.warn(ans);
          return ans;
        },
      },
      methods:{
        pickWord:function (word) {
          /*console.log(this.words);
          let buf = this.words;
          buf[buf.length-1]=word;
          this.val = buf.join(', ')+', ';*/
          if (this.pickedArr.length >= this.pickLim){
            alert('Допустимо не более '+this.pickLim+' позиций!');
            return
          }
          let buf = this.arr.filter(variant=>word==variant.name);
          //console.log(buf);
          if (buf.length != 1) {
            console.error('Unknown variant is picked!');
            return
          }
          this.pickedArr.push(buf[0]);
          this.val='';
          this.updateWords();
        },
        updateWords:function () {
          //console.log(this.words);
          let matArr = this.arr.filter(variant => this.words.includes(variant.name));
          //console.log(matArr);
          this.idArr=[];
          for (let p in this.pickedArr){
            this.idArr.push(this.pickedArr[p].id);
          }
          //if(this.idArr.length==this.words.filter(word => word!='').length) this.valid = true; else this.valid = false;
          //this.val = this.words.join(', ')
          if (this.lastWord.length<this.len) this.showVariants=false; else
          if (this.displayed.length<1) this.showVariants=false; else this.showVariants=true;
          //this.$emit('done', this.idArr);
        },
        removeVariant: function(variant){
          this.pickedArr.splice(this.pickedArr.indexOf(variant),1);
          this.updateWords();
        },
        yell:function () {
          console.log("AAAAAAAA");
        }
      },
      watch:{
        arr: function () {
          this.displayed;
        },
        val: function () {
          this.updateWords();
          //console.log('val watch');
        }
      }
    }
</script>

<style lang="sass" scoped>
    $lgray: #f6f6f6
    $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
    .bg
      background-color: darkgray
      opacity: 0.3
      position: fixed
      left: 0
      top: 0
      height: 100vh
      width: 100vw
      z-index: 2
    .PickBar
      .body
        width: inherit
        height: inherit
        background-color: white
        border: black solid 0.1px
        position: fixed
        z-index: 2
        .topPanel
          //background: lightsalmon
          input
            border: none
            //background: yellow
            font-family: inherit
            font-size: inherit
            margin: 5px 0px 5px 10px
            width: calc(100% - 20px)
            //padding: 0
            //width: 100%
            //height: 100%
            &:focus
              outline: none
          .pickedBar
            height: 60px
          .picked
            display: inline-block
            margin: 5px 0px 5px 10px
            .text
              cursor: pointer
              &:hover
                color: red
          .invalid
            //background-color: lightpink
        .variants
          background-color: white
          border: black solid 0.1px
          //border-top: none
          //height: 60px
          //width: 100%
          //height: 250px
          overflow-y: hidden
          .variant
            margin-left: 10px
            color: darkgray
            cursor: pointer
            &:hover
              color: red
</style>



