<template>
  <div class="UserBar">
    <span class="username" @click="showOptions=1-showOptions">{{username}}</span>
    <div v-if="showOptions" class="options">
      <div class="option" @click="logout(); showOptions=0">Выйти</div>
      <div class="option" @click="showLoginBlock=true; showOptions=0">Сменить пользователя</div>
    </div>
    <img :src="host+'/static/img/shared/basket-32.png'" />
    <img :src="host+'/static/img/shared/star-32.png'"/>
    <login-block @hideLoginBlock="showLoginBlock=false" :shown="showLoginBlock"></login-block>
  </div>
</template>

<script>
    import LoginBlock from "./LoginBlock";
    import {ax,host} from './main.js';
    export default {
      name: "UserBar",
      components: {LoginBlock},
      data: function () {
        return {
          username: 'mamie-t',
          showLoginBlock: false,
          showOptions: 0,
          host: host,

        }
      },
      methods: {
        logout: function () {
          ax.get("/shared/get-csrf-token")
            .then(function (data1) {
                console.log(data1.data);
                var fd = new FormData;
                fd.append('csrfmiddlewaretoken', data1.data);
                ax.post("/account/logout", fd, {
                  headers: {
                    'X-CSRFToken': data1.data,
                    'Content-Type': 'multipart/form-data'
                  }
                })
                .then(function (data) {
                    window.location.reload();
                    console.log(data.data);
                  }
                )
                .catch(function (data) {
                    console.warn(data.response.data);
                  }
                )
              }
            )
            .catch(function (data) {
                console.warn(data.response.data);
              }
            )
        },


      },
      created: function () {
        let vm = this;
        ax.get(host + '/account/about-me')
          .then(function (data) {
              console.log(data.data);
              vm.username = data.data.username;
              //vm.$forceUpdate();
            }
          );

      },
    }
</script>

<style lang="sass" scoped>
  $shadow: 0px 2px 5px rgba(0, 0, 0, 0.5)
  .UserBar
    //border: solid red 1px
    //margin: 20px
    //background-color: orangered
    display: inline
    float: right
    margin: 20px 48px 0 0
    .options
      position: fixed
      background-color: white
      box-shadow: $shadow
      font-size: 18px
      right: 100px
      top: 100px
      padding: 10px
      .option
        padding: 5px
        cursor: pointer
        &:hover
          color: blue
    img
      height: 32px
      float: right
    .username
      //margin-right: 20px
      //margin-left: 80px
      float: left
      margin-top: 5px
      font-size: 18px
</style>



